			animationData: {},
			animationDataArr: [{}, {}, {}, {}, {}]
		}
	},
	onUnload() {
		// 页面卸载的时候清除动画数据
		this.animation = {};
		this.animationDataArr = [{}, {}, {}, {}, {}];
	},

	onLoad() {

		// 在页面创建的时候，创建临时的动画
		this.animation = uni.createAnimation()



		// let baseUrl=this.baseUrl;
